<template>
  <div>
    h1>{{add}}</h1>
    <input type="text" v-model="add">
  </div>
</template>

<script>
export default {
  data() {
    return {
      good: 123
    }
  },
  computed: {
    add: {
      get() {
        return this.good
      },
      set(newValue) {
        this.good = newValue
      }
    }
  },
}
</script>

